  align-items: stretch;
  border: 0 solid #000;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  height: 100%;
  z-index: 0;
}


.global-section {
  -webkit-text-size-adjust: 100%;
  color: rgba(38, 38, 38, 1);
  color: rgba(var(--i1d, 38, 38, 38), 1);
  align-items: stretch;
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.global-sub-section {
  min-height: 100%;
  overflow: hidden;
}

.svg-button-container {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0px solid #000;
  align-items: stretch;
}

.svg-button {
  font-size: 14px;
  align-items: center;
  background: 0 0;
  border: 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  padding: 0;
  outline: none;
}

.svg-container {
  cursor: pointer;
  border: 0 solid #000;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
}

.svg-button-span {
  animation-duration: .45s;
  animation-name: like-button-animation;
  animation-timing-function: ease-in-out;
  transform: scale(1);
}

.drop-down-arrow {
  align-items: stretch;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 1);
  background: rgba(var(--d87, 255, 255, 255), 1);
  border: 1px solid rgba(255, 255, 255, 1);
  border: 1px solid rgba(var(--f23, 255, 255, 255), 1);
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.0975);
  box-shadow: 0 0 5px 1px rgba(var(--jb7, 0, 0, 0), 0.0975);
  height: 14px;
  position: absolute;
  transform: rotate(45deg);
  width: 14px;
  bottom: 0;
  top: -6px;
  left: 427px;
}

@keyframes like-button-animation {
  0%,
  100% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(0.95);
  }
}

section .home-page-main {
  position: relative;
}

section .home-page-main .main-home-section {
